<template>
	<div class="ask">
		<topic-list :items='items'></topic-list>
		<infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>
  
	</div>
</template>

<script>
	//引入去数据的模块
	import {getTopics} from '@/getdata/getTopic.js'
	//引入主题列表组件
	import topicList from '@/components/topics/List1'
	//引入分页滑动的组件
	import InfiniteLoading from 'vue-infinite-loading'
	//引入 处理时间的组件
	import {processDate} from '@/getdata/processDate.js'
	
	export default {
		name:'Ask',
		data(){
			return{
				items:[],
				pages:1,
				isShow:true
			}
		},
		//加载组件
		components:{
			topicList,
			InfiniteLoading
		},
		mounted:function(){
			getTopics({tab:'ask',limit:20,page:this.page}).then((response)=>{
				
				
			for(var i=0;i<response.data.data.length; i++){
//				console.log(response.data.data[i].last_reply_at)
				response.data.data[i].last_reply_at = processDate(response.data.data[i].last_reply_at)
			}
			
			
			this.items = response.data.data
			})
		},
		methods:{
			infiniteHandler($state){
				setTimeout(()=>{
					getTopics({tab:'ask',limit:10,page:++this.pages}).then((response)=>{
						for(var i=0;i<response.data.data.length; i++){
				
//							console.log(response.data.data[i].create_at)
							response.data.data[i].last_reply_at = processDate(response.data.data[i].last_reply_at)
						}
						this.items = this.items.concat(response.data.data)
//						console.log(this.items)
						if(response.data.data.length == 0){
							this.isShow = false
							return
						}
						$state.loaded()
					})
				},500)
			}
		}
	}
</script>

<style scoped lang="scss">
  // 引入 公共样式
  @import '../../assets/sass/base.scss';

  .ask{
    margin-top: rem(190px);
    margin-bottom: rem(130px);
  }  
</style>


