<template>
  <div id="app">
  	<nav class="header">
  		<img src="../static/images/logo.svg" alt="" />
  		<div class="search">
  			<span class="iconfont icon-sousuotianchong"></span>
  			<input id="in" type="text" @focus="inputClick()" @blur="inputBlur()"/>
  		</div>
  	</nav>
    <!--中间内容-->
    <router-view/>
    <nav class="footer">
      <ul>
        <router-link to="/" tag="li" :class="resPath=='index'?'mo':''">
          <span class="iconfont icon-shop"></span>
          <p>首页</p>
        </router-link>
        <router-link to="/new" tag="li" :class="resPath=='new'?'mo':''">
          <span class="iconfont icon-menu"></span>
          <p>新手入门</p>
        </router-link>
        <router-link to="/login" tag="li" :class="resPath=='login'?'mo':''">
          <span class="iconfont icon-addressbook"></span>
          <p>登录</p>
        </router-link> 
      </ul>  
    </nav>
  </div>
</template>

<script>
export default {
  name: 'App',
  //计算属性
  computed:{
  	resPath:function(){
  		var arr = this.$route.path.split("/")
  		return arr[1]
  	}
  },
  methods:{
  	inputClick:function(){
//	console.log(this)//是#app
//		this.style.backgroundColor = '#fff';

			document.getElementById("in").style.backgroundColor = '#fff';
  	},
  	inputBlur:function(){
  		document.getElementById("in").style.backgroundColor = '#888888';
  	}
  }
}
</script>

<style lang="scss">
//引入 公共样式
@import './assets/sass/base.scss';
//顶部导航
nav.header{
	position:fixed;
	bottom:0;
	top:0;
	display: flex;
	align-items: center;
	height:rem(100px);
	width:100%;
	background: #444444;
	img{
		margin:0 rem(30px);
		font-size:rem(30px);
		vertical-align: initial;
		font-weight:700;
		width:rem(200px);
		height:rem(100px);
	}
	.search{
		position: relative;
		flex:1;
		margin:0 rem(60px) 0 rem(30px);
		input{
			text-indent: rem(40px);
			margin-top:rem(5px);
			width:100%;
			height:rem(45px);
			border-radius: rem(20px);
			background: #888888;
		}
		span.iconfont{
			position: absolute;
			top:rem(12px);
			left:rem(10px);
			font-size:rem(25px);
			color:#A4A4A4;
		}
	}
}


//底部导航
nav.footer{
	position:fixed;
	bottom:0;
	left:0;
	width:100%;
	height:rem(100px);
	background:#222;
	border-top:rem(1px) solid #333;
	ul{
		display:flex;
		
		li{
			text-align: center;
			width:33%;
			color:#777;
			border-radius: rem(10px);
			span{
				font-size:rem(50px);
			}
			p{
				font-size:rem(30px);
				font-size:rem(30px);
			}
			&.mo{
				color:#FFFFFF;
			}
		}
		
	}
}


</style>



