import Vue from 'vue'
import Router from 'vue-router'

//引入 一级组件
import Index from '@/components/Index'
import New from '@/components/New'
import Login from '@/components/Login'

//index页面的  子组件
import All from '@/components/topics/All'
import Ask from '@/components/topics/Ask'
import Good from '@/components/topics/Good'
import Job from '@/components/topics/Job'
import Share from '@/components/topics/Share'
import Client from '@/components/topics/Client'

//详情页组件
import Details from '@/components/topics/Details'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: Index,
      children:[
      	{
      		path:'/',
      		component:All
      	},{
      		path:'good',
      		component:Good
      	},{
      		path:'share',
      		component:Share
      	},{
      		path:'ask',
      		component:Ask
      	},{
      		path:'job',
      		component:Job
      	},{
      		path:'client',
      		component:Client
      	},{
		  		path:'details/:id',
		  		component:Details
		    }
      ]
    },{
    	path: '/new',
    	component: New
    },{
    	path: '/login',
    	component: Login
    },{
    	path:'/*',
    	redirect:'/index'
    }
  ]
})



