<template>
	<div class="list">
		<!--loading 组件-->
		<loading v-if='!items.length'></loading>
		<!--主题列表-->
		<ul class="items">
			<li class="item" v-for="item in items" >
				<img v-lazy="item.author.avatar_url" alt="" />  
				<div class="tab">
		  			<div class="mo" v-if='item.top'>置顶</div>
		  			<div class="mo" v-else-if='item.good'>精华</div>
		  			<div v-else>{{tabs[item.tab]}}</div>
		  		</div>
		  		<div class="title">
		  			<router-link tag='p' :to="'/index/details/'+item.id">{{item.title}}</router-link>
		  			<div class="click-reply">
		  				<span class='click'>{{item.reply_count}}</span>/
		  				<span class="reply">{{item.visit_count}}</span>
		  			</div>
		  		</div>
		  		<div class="last-time">{{item.last_reply_at}}</div>
		  		
			</li>
		</ul>
	</div>
</template>

<script>
	//引入 取数据的模块
	import {getTopics} from '@/getdata/getTopic.js'
	//引入 Loading 的组件
	import loading from '@/components/Loading'
	
	export default {
		name: 'List',
		data(){
			return{
				tabs:{
			      	ask:'问答',
			      	job:'招聘',
			      	share:'分享'
				}
			}
		},
	  //计算属性
	  computed:{
	  	resPath:function(){
	  		var arr = this.$route.path.split("/")
	  		return arr
	  	}
	  },
		components:{
			loading
		},
		//接收 items
		props:['items'],
		mounted:function(){
			
		}
	}
</script>

<style scoped lang="scss">
	
//引入 公共样式
@import '../../assets/sass/base.scss';

//引入  主题列表的样式
@import "../../assets/sass/itemlist.scss";
</style>


