<template>
  <div class="login">
  	<div>
  		<div class="top"><span>首页</span>/<span>登录</span></div>
		  <div class="content">
		  	<div>
		  		<p>用户名</p>
		  		<input type="text" />
		  	</div>
		  	<div>
		  		<p class='pwd'>密码</p>
		  		<input type="text" />
		  	</div>
		  	<div class="but"><button>登录</button><button>通过  GitHub 登录</button> <span>忘记密码了?</span></div>
		  </div>
  	</div>
	  
  </div>
</template>

<script>
export default {
  name: 'Gzh',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
	
  // 引入 公共样式
  @import '../assets/sass/base.scss';
	
	.login{
			height:rem(1200px);
			background:#E1E1E1;
			padding:rem(35px) rem(10px) 0;
		 	margin-top: rem(100px);
    	>div{
    		border-radius: rem(10px);
    		background:#fff;
    		/*height:rem(600px);*/
    		.top{
    			height:rem(70px);
    			line-height:rem(70px);
    			padding-left:rem(6px);
    			font-size:rem(27px);
    			background:#F6F6F6;
    			color:#888888;
    			span{
    				margin:0 rem(12px);
    			}
    			span:nth-child(1){
    				color:#80BD01;
    			}
    			span:nth-child(2){
    				color:#888888;
    			}
    		}
    		.content{
    			margin:rem(100px) rem(20px);
    			padding-bottom:rem(200px);
    			p{
    				font-size:rem(28px);
    				margin-bottom: rem(10px);
    			}
    			input{
    				text-indent: rem(15px);
    				font-size:rem(30px);
    				color:#000;
    				border-radius: rem(10px);
    				border:rem(2px) solid #c5c5c5;
    				width:rem(600px);
    				height:rem(50px);
    			}
    			.pwd{
    				margin-top:rem(40px);
    			}
    			.but{
    				margin-top:rem(80px);
    				button{
    					padding:0 rem(20px);
    					font-size:rem(25px);
    					height:rem(60px);
    					line-height:rem(60px);
    					border-radius: rem(6px);
    					border:none;
    					color:#fff;
    				}
    				button:nth-child(1){
    					background:#0088CC;
    					margin-right:rem(10px);
    				}
    				button:nth-child(2){
    					background:#5BC0DE;
    					margin-right:rem(30px);
    				}
    				span{
    					color:#999;
    					font-size: rem(28px);
    				}
    			}
    		}
    	} 
		
	}


</style>



