<template>
	<div class="details">
		<!--loading 组件-->
		<loading v-if='!item'></loading>
		<!--<infinite-loading v-if="isShow" @infinite="infiniteHandler"></infinite-loading>-->
		<!--文章内容-->
		<div class="details-wrap" v-if='item'>
			<div class="header">
				<div class="header-top">
					<div class="tab">
			  			<div class="mo" v-if='item && item.top'>置顶</div>
			  			<div class="mo" v-else-if='item && item.good'>精华</div>
			  			<div v-else>{{item && tabs[item.tab]}}</div>
			  		</div>
			  		<div class="title">{{item && item.title}}</div>
				</div>
				<div class="header-two">
					<i></i><span>发布于 {{item && item.last_reply_at}}</span>
					<i></i><span>作者  {{item && item.author.loginname}}</span>
					<i></i><span>{{item && item.visit_count}} 次浏览</span>
					<i></i><span>来自  {{item && tabs[item.tab]}}</span>
				</div>
			</div>
			<div class="content" v-html='item && item.content'></div>
		</div>
		<!--多少条回复  start-->
		<div class="details-com"  v-if='item'>
			<div class="com">{{item && item.replies.length}} 回复</div>
			<ul>
				<li v-for="item in (item && item.replies)">
					<div class="top">
						<img :src="item.author.avatar_url" alt="" />
						<p class='p1'>{{item && item.author.loginname}}</p>
						<p class='p2'>1楼<i></i>{{item && item.create_at}}</p>
						<p class='p3' v-if="item.ups.length"><span class="iconfont icon-zan"></span>{{item && item.ups.length}}</p>
					</div>
					<div class="two" v-html='item && item.content'></div>
				</li>
			</ul>
		</div>
		
		
		
		
		
	</div>
</template>

<script>
	//引入 取数据的模块
	import {getTopicData} from '@/getdata/getTopic.js'
//	//引入分页滑动的组件
	import InfiniteLoading from 'vue-infinite-loading'
	//引入 处理时间的组件
	import {processDate} from '@/getdata/processDate.js'
	//引入 Loading 的组件
	import loading from '@/components/Loading'
	
	export default {
		name:'Details',
		data(){
			return{
				item:null,
				pages:1,
				isShow:true,
				tabs:{
			      	ask:'问答',
			      	job:'招聘',
			      	share:'分享'
				}
			}
		},
		//计算属性
		computed:{
			resPath:function(){
	  		var arr = this.$route.path.split("/")
	  		return arr[3]
	  		
  			}
		},
		//加载组件
		components:{
			loading,
			InfiniteLoading
		},
		mounted:function(){
			var id = this.$route.params.id
			getTopicData(id).then((response)=>{
				response.data.data.last_reply_at = processDate(response.data.data.last_reply_at)
				var comm = response.data.data.replies
				for(var i=0;i<comm.length;i++){
					comm[i].create_at = processDate(comm[i].create_at)
//					console.log(comm[i].create_at)
				}
				this.item = response.data.data
//				console.log(this.item)
			})
		},
		methods:{
//			infiniteHandler($state){
//				setTimeout(()=>{
//					getTopics({tab:'all',limit:10,page:++this.pages}).then((response)=>{
//						for(var i=0;i<response.data.data.length; i++){
//				
////							console.log(response.data.data[i].create_at)
//							response.data.data[i].last_reply_at = processDate(response.data.data[i].last_reply_at)
//						}
//						this.items = this.items.concat(response.data.data)
////						console.log(this.items)
//						if(response.data.data.length == 0){
//							this.isShow = false
//							return
//						}
//						$state.loaded()
//					})
//				},500)
//			}
		}
	}
</script>

<style lang="scss">
  // 引入 公共样式
  @import '../../assets/sass/base.scss';
  // 引入 details 的样式 
  @import '../../assets/sass/details.scss';
  .details{
    margin-top: rem(190px);
    margin-bottom: rem(130px);
    background:#E1E1E1;
    padding:0 rem(7px);
    overflow: hidden;
  }  
</style>





